import './App.css';
import { Home, Room, JoinRoom } from './views'
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";

function App() {
  return (
    <Router>
      <Switch>
        <Route path="/" exact component={Home}>
        </Route>
        <Route path="/room/:link" exact component={Room}>
        </Route>
        <Route path="/joinroom/:link" exact component={JoinRoom}>
        </Route>
        <Route path="/test" exact>
          <div>Test</div>
        </Route>
      </Switch>
    </Router>
  );
}

export default App;
